/*
	Layout
*/
/*----------------------------------*    Grid
\*----------------------------------*/
/*
	Container and row
*/
/* Container main css and it's width */
.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 1rem;
  padding-left: 1rem;
  /*
		Responsive media query
		On Extra-Small screens and down, container is full width
	*/
  /* Width 49rem which equals 735px - based on html font-size 15px */
  /* Width 65rem which equals 975px - based on html font-size 15px */ }
  @media only screen and (min-width: 48em) {
    .container {
      width: 49rem; } }
  @media only screen and (min-width: 64em) {
    .container {
      width: 65rem; } }

/* Fluid container aka full width */
.container-fluid {
  margin-right: auto;
  margin-left: auto;
  padding-right: 1rem;
  padding-left: 1rem; }

/* Row is wraper of columns, it removes extra padding of columns left and right to fit container width */
.row {
  margin-right: -1rem;
  margin-left: -1rem; }
  .row:after, .row:before {
    display: table;
    content: ''; }
  .row:after {
    clear: both; }

/*
	Columns
*/
/* Add padding left and right to all columns. Width: 100% added to fill the container if any column size is not defined */
[class*=' col-'],
[class^='col-'] {
  float: left;
  width: 100%;
  padding-right: 1rem;
  padding-left: 1rem; }

